import { default as React, useEffect, useRef } from "react"
import { animated, useTransition } from "react-spring"
import { createUniversalPortal } from "../utils/portal"
const Modal = ({
  isOpen,
  onRequestClose,
  children,
  style,
  styleInner,
  inline,
  color,
  title,
  icon,
  center,
  styleBg,
  header,
}) => {
  const content = useRef(null)

  useEffect(() => {
    if (isOpen) {
      focusContent()
    }
  }, [isOpen])

  const focusContent = () => {
    content.current && content.current.focus()
  }

  const requestClose = (event) => {
    onRequestClose && onRequestClose(event)
  }

  const transition = useTransition(isOpen, {
    from: {
      opacity: 0,
      transform: center ? "translateY(20px)" : "translate(0%, -34%)",
    },
    enter: {
      opacity: 1,
      transform: center ? "translateY(0px)" : "translate(0%, -50%)",
    },
    leave: {
      opacity: 0,
      transform: center ? "translateY(-20px)" : "translate(0%, -34%)",
    },
  })

  return transition(
    (props, item) =>
      item && (
        <animated.div
          onClick={requestClose}
          style={{
            ...props,
            ...styleBg,
          }}
        >
          {children}
        </animated.div>
      ),
  )
}

class ModalPortal extends React.Component {
  render() {
    return createUniversalPortal(<Modal {...this.props} />, "body")
  }
}

export default ModalPortal
